//
import {
	getTableData
} from './getTableData';
//
import {
	overviewOut
} from './overviewOut';

/**
 * 
 * @method setLimit
 * @param param {Object} 
 * @param pagination {Object} 
 * @param channel {String} 
 * @return {Function}
 */
const setLimit = (param, pagination, channel) => {

	return function(dispatch) {
		// 
		dispatch({
			type: "PROFILE_LIST_CURRENT",
			payload: 1
		})

		//
		dispatch({
			type: "PROFILE_LIST_PAGE_SIZE",
			payload: pagination.pageSize
		})


		if (param.type == 3) {
			//3
			var data = {
				appCode: "",
				channelName: channel,
				startDate: "2017-12-16",
				endDate: "2018-01-14",
				channelCategory: "",
				offset: 1,
				limit: 10,
				type: param.type
			}

			overviewOut(data, dispatch);
		} else {
			//1,2 
			var data = {
				appCode: "",
				channelGroup: "samsung_total_news",
				channelName: "",
				startDate: "2017-12-12",
				endDate: "2018-01-10",
				channelCategory: "",
				offset: 1,
				limit: pagination.pageSize,
				type: param.type
			}

			getTableData(data, dispatch);
		}
	}
}

export {
	setLimit
}


