import React from 'react';
//
import {
	overviewOut
} from './overviewOut';

//
import {
	getTableData
} from './getTableData';


/** 
 * 
 * @method setType 
 * @param param {Object} 
 * @param id {Number} 
 * @return {Function}
 */
const setType = (data) => {

	return function(dispatch) {
		//
		dispatch({
			type: "PROFILELIST_TYPE",
			payload: data.type
		});

		//
		dispatch({
			type: "PROFILE_LIST_CURRENT",
			payload: 1
		})

		//
		dispatch({
			type: "PROFILE_LIST_PAGE_SIZE",
			payload: 10
		})


		if (data.type == 1) {
			//1
			dispatch({
				type: "PROFILELIST_TABLE_COLUMNS",
				payload: [{
					title: '',
					dataIndex: 'day'
				}, {
					title: '',
					dataIndex: 'newUser'
				}, {
					title: '',
					dataIndex: 'qualityPer',
					render: (text) => (<span> {(Number(text) * 100).toFixed(2) + '%'} </span>)
				}, {
					title: '',
					dataIndex: 'activeUser'
				}, {
					title: '',
					dataIndex: 'visitUser'
				}, {
					title: '',
					dataIndex: 'loseUser'
				}, {
					title: '',
					dataIndex: 'backUser'
				}, {
					title: '',
					dataIndex: 'outUser'
				}]
			});

		} else if (data.type = 2) {
			//2
			dispatch({
				type: "PROFILELIST_TABLE_COLUMNS",
				payload: [{
					title: '',
					dataIndex: 'day'
				}, {
					title: '',
					dataIndex: 'channel'
					// ,
					// render: (text, record) => (<a href="javascript:void(0);" onClick={channelNameFn}>{text}</a>)
				}, {
					title: '',
					dataIndex: 'newUser'
				}, {
					title: '',
					dataIndex: 'qualityPer',
					render: (text) => (<span> {(Number(text) * 100).toFixed(2) + '%'} </span>)
				}, {
					title: '',
					dataIndex: 'activeUser'
				}, {
					title: '',
					dataIndex: 'visitUser'
				}, {
					title: '',
					dataIndex: 'loseUser'
				}, {
					title: '',
					dataIndex: 'backUser'
				}, {
					title: '',
					dataIndex: 'outUser'
				}]
			});
		} else if (data.type = 3) {
			//3
			dispatch({
				type: "PROFILELIST_TABLE_COLUMNS",
				payload: [{
					title: '',
					dataIndex: 'day'
				}, {
					title: '',
					dataIndex: 'newChannel'
				}, {
					title: '',
					dataIndex: 'countUser'
				}]
			});
		}

		// dispatch({
		// 	type: "PROFILELIST_TABLE_COLUMNS",
		// 	payload: id
		// });

		if (data.type == 3) {

			//
			var ajaxData = {
				appCode: data.appCode,
				channelName: data.channelName,
				startDate: data.startDate,
				endDate: data.endDate,
				channelCategory: data.channelCategory,
				offset: data.offset,
				limit: data.limit,
				type: data.type
			}

			overviewOut(ajaxData, dispatch);

		} else {

			//
			var ajaxData = {
				appCode: data.appCode,
				channelGroup: data.channelGroup,
				channelName: data.channelName,
				startDate: data.startDate,
				endDate: data.endDate,
				channelCategory: data.channelCategory,
				offset: data.offset,
				limit: data.limit,
				type: data.type
			}

			getTableData(ajaxData, dispatch);

		}
	}
}

export {
	setType
}


