// 
import {
	getTableData
} from './getTableData';

//
import {
	overviewOut
} from './overviewOut';

/**
 * 
 * @method setPagination
 * @param param {Object} 
 * @param pagination {Object} 
 * @channel channel 
 */
// const setPagination = (param, pagination, channel) => {
const setPagination = (data) => {

	return function(dispatch) {
		//
		dispatch({
			type: "PROFILE_LIST_CURRENT",
			payload: data.offset
		})
		//
		dispatch({
			type: "PROFILE_LIST_PAGE_SIZE",
			payload: data.limit
		})


		if (data.type == 3) {
			//
			var dataAjax = {
				appCode: data.appCode,
				channelName: data.channel,
				startDate: data.startDate,
				endDate: data.endDate,
				channelCategory: data.channelCategory,
				offset: data.offset,
				limit: data.limit,
				type: data.type
			}
			overviewOut(dataAjax, dispatch);
		} else {
			//
			var dataAjax = {
				appCode: data.appCode,
				channelGroup: data.channelGroup,
				channelName: data.channelName,
				startDate: data.startDate,
				endDate: data.endDate,
				channelCategory: data.channelCategory,
				offset: data.offset,
				limit: data.limit,
				type: data.type
			}
			getTableData(dataAjax, dispatch);
		}
	}
}

export {
	setPagination
}


