/**
 * antdesigns Tablekey
 * @method addKey
 * @param data {Array} 
 * @param str {Array} 
 * @return arr {Array} [{}]
 */
function addKey(data, str) {
	var arr = [];

	data.map((v, k) => {
		v.key = str + k;
		arr.push(v);
	});

	return arr;
}

export {
	addKey
}


