const getDownLoadData = (data) => {
	return function(dispatch) {



		if (data.type == 1 || data.type == 2) {
			//
			reqwest({
				url: '/data/overview/list.do?type=' + data.type,
				method: 'post',
				data: data,
				type: 'json'
			}).then((msg) => {



				if (msg.status) {
					dispatch({
						type: "PROFILE_LIST_EXCEL",
						payload: msg.data
					})
				} else {
					if (msg.code == -1) {
						window.location.href = "/"
					}
				}
			});
		} else {
			//
			reqwest({
				url: '/data/overview/out.do',
				method: 'post',
				data: data,
				type: 'json',
			}).then((msg) => {
				if (msg.status) {
					dispatch({
						type: "PROFILE_LIST_EXCEL",
						payload: msg.data
					})

				} else {
					if (msg.code == -1) {
						alert(-1);
						window.location.href = "/"
					}
				}
			});
		}


	}
};



